
.box {
    position:absolute;
    top:0px;
    bottom:0px;
    left:0px;
    right:0px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack:center;
        -ms-flex-pack:center;
            justify-content:center;
    -webkit-box-orient:vertical;
    -webkit-box-direction:normal;
        -ms-flex-direction:column;
            flex-direction:column;
}
.box-h {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack:center;
        -ms-flex-pack:center;
            justify-content:center;
    -webkit-box-orient:horizontal;
    -webkit-box-direction:normal;
        -ms-flex-direction:row;
            flex-direction:row;
}
.search-input {
    border:1px solid black;
    width:80%;
    padding:12px;
    padding-top:14px;
    padding-bottom:10px;
    border-radius:50px;
    outline:none;
    background-color: transparent;
}
::-webkit-input-placeholder { /* WebKit browsers */
  color:#000;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color:#000;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
  color:#000;
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
  color:#000;
}
.sitem {
  background: #ccc;
}
.s-li {
  cursor: pointer;
  padding: 8px;
  margin: 0px;
}
.s-ul {
  list-style-type:none;
  padding: 0px;
  margin: 0px;
  width: 100%;
  background: #efefef;
}

